import { HashMap } from '../../../shared/utilities/types.utilities';
import { ISpaceAvailabilityOptions } from './space.class';

import * as dayjs from 'dayjs';

export function availabilityOptionsToQuery(options: ISpaceAvailabilityOptions): HashMap {
    let query: HashMap = {};
    if (options) {
        query = { ...options };
        if (options.date) {
            const date = dayjs(options.date).startOf('m');
            query.available_from = date.unix();
            query.available_to = date.add(options.duration || 60, 'm').unix();
            delete query.date;
            delete query.duration;
        }
    }
    return query;
}
